/* Home.css will be for desktop 1920 */




@media only screen and (max-width: 1200px){
/* For laptop screens */
    .SketchContainer{
        margin-top:3.5vh;
        width: 77vw;
        height: 66vh;
    }
    .SketchSide{
        transform:translateX(-28%);
    }

    .InfoProjects{
        margin-left: 5vw;
    }

    .ProjectsHeader{
        padding-top: 0.5em;
        padding-left:0.5em;
        font-size: 2.7vw;
    }
}

@media only screen and (max-width: 1024px){
    /* For small screens*/
    .SketchContainer{
        margin-top:3.5vh;
        width: 77vw;
        height: 66vh;
    }
    .SketchSide{
        transform:translateX(-28%);
    }
    .InfoProjects_Hidden{
        grid-column: 6/13;
    }
    
    .InfoProjects{
        grid-column: 6/13;
    }
    
    .ProjectsList{
        grid-template-columns:repeat(2,1fr); 
        grid-row-gap: 2em;
        padding-top:5vw;
    }

}

@media only screen and (max-width: 768px){
    /* For tablets screens*/

    .InfoName{
        grid-column: 1/5;
        grid-row: 5/7;
        margin-left: 0vw;
        font-size: 5vw;
    }
    .InfoNameIntro{
        padding: 1vw;
        margin-top: 2vw;
        font-size: 1.8vw;
    }

    .SketchContainer{
        margin-top:3.5vh;
        width: 77vw;
        height: 80vh;
    }
    .SketchSide{
        transform:translateX(-28%);
    }
    .InfoProjects_Hidden{
        grid-column: 6/13;
    }
    
    .InfoProjects{
        grid-column: 6/13;
        backdrop-filter: invert(70%);
    }
    .ProjectsHeader{
        font-size: 2.9vw;
    }
    .ProjectsList{
        grid-template-columns:1fr; 
        grid-row-gap: 2em;
        padding-top:5vw;
    }
    .InfoProjectsLink{
        font-size: 3vh;
    }

}

@media only screen and (max-width: 481px){
    /* For tablets screens*/

    .InfoName{
        grid-column: 1/5;
        grid-row: 5/7;
        margin-left: 0vw;
        font-size: 4vh;
    }
    .InfoNameIntro{
        padding: 1vw;
        margin-top: 2vw;
        font-size: 1.5vh;
    }

    .SketchContainer{
        margin-top:3.5vh;
        width: 79vw;
        height: 80vh;
    }
    .SketchSide{
        transform:translateX(-28%);
    }
    .InfoLinks{
        justify-self:start;
        align-self: center;
        grid-column: 2/3;
    }

    .InfoProjects_Hidden{
        grid-column: 6/13;
    }
    
    .InfoProjects{
        grid-column: 6/13;
        background-color: transparent;
        backdrop-filter: sepia(100%);
        overflow-x: hidden;
    }
    .ProjectsHeader{
        font-size: 4.9vw;
        position: absolute;
    }
    .ProjectsList{
        grid-template-columns:1fr; 
        grid-row-gap: 2em;
        padding-top:5vw;
    }
    .InfoProjectsLink{
        font-size: 3vh;
    }

}



