/* Project.css will be for desktop sidze */

@media only screen and (max-width: 1200px){
    /* For large screens */

    .ProjectImageContainer{
       width:9vw;
       height:9vw;
    }
    
    .ProjectImage{
        width:9vw;
        height: 9vw;
     }
    .ProjectTextContainer{
        width:9vw;
        height: 9vw;
    }
}

@media only screen and (max-width: 1024px){
    /* For small screens*/
    .ProjectImageContainer{
       width:12vw;
       height:12vw;
    }
    
    .ProjectImage{
        width:12vw;
        height: 12vw;
     }
    .ProjectTextContainer{
        width:12vw;
        height: 12vw;
    }
    .ProjectDescription{
        font-size:1.2vw;
    }
    .ProjectUsed{
        font-size: 1.2vw;
    }
}

@media only screen and (max-width: 768px){
    /* For small screens*/
    .ProjectImageContainer{
       width:20vh;
       height:20vh;
    }
    
    .ProjectImage{
        width:20vh;
        height: 20vh;
     }
    .ProjectTextContainer{
        width:20vh;
        height: 20vh;
    }
    .ProjectDescription{
        font-size:1.9vh;
    }
    .ProjectUsed{
        font-size: 1.97vh;
    }
    .ProjectTitle{
        font-size:2.2vh;
    }
    
}

@media only screen and (max-width: 481px){
    /* For small screens*/
    .ProjectImageContainer{
       width:20vh;
       height:20vh;
       
    }
    
    .ProjectImage{
        width:20vh;
        height: 20vh;
     }
    .ProjectTextContainer{
        width:20vh;
        height: 20vh;
        
    }
    .ProjectDescription{
        font-size:1.9vh;
    }
    .ProjectUsed{
        font-size: 1.97vh;
    }
    .ProjectTitle{
        font-size:2.2vh;
    }
    
}