
.ProjectContainer{
    display:grid;
    grid-template-rows: 1fr auto;
    grid-template-columns: 1fr;
    grid-row-gap: 0.5em;
    justify-items: center;
}
.ProjectImageContainer{
   position: relative;
   grid-row:1/2;
   width:8.8vw;
   height:8.8vw;
   border-radius: 10%;
   justify-items: center;
   text-align:center;
}


.ProjectImage{
    position: absolute;
    z-index: -1;
    width:8.8vw;
    height: 8.8vw;
    border-radius: 10%;
    transform:translateX(-50%);
 }
.ProjectTextContainer{
    position: absolute;
    z-index: 1;
    display: grid;
    width:8.8vw;
    height: 8.8vw;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    justify-items: center;
    align-items: center;
    opacity: 0;
    backdrop-filter: sepia(100%);
    transition: 0.5s;
}
.ProjectDescription{
    grid-row: 1/2;
    justify-self: center;
    background-color:black;
    color: white;
    font-family: 'courier';
    font-size:1vw;
    text-decoration:none;
    text-align:center;
    width: 100%;
}
.ProjectUsed{
    grid-row: 2/3;
    justify-self: center;
    background-color: black;
    color:yellow;
    font-family: 'courier';
    font-size:1vw;
    text-decoration:none;
    text-align:center;
    width: 100%;
}

.ProjectTextContainer:hover{
   opacity: 1;
   cursor: pointer;
}


.ProjectTitle{
    grid-row:2/3;
    color: white;
    font-family: 'courier';
    font-size:1.2vw;
    text-align: center;
}
